import goodslist from '@/pages/goodslist/goodslist.vue'
import {canshu} from '@/plugin/canshu.js'
export default {

	name: 'goods',

	props: {},
	data() {
		return {
			active:0,
			goodscanshu:[]
		};
	},

	methods: {
		Submission(val){
			this.$store.commit('changetitle',val);
			console.log(this.$store.state.goodstitle)
		},
		
	},

	computed: {},

//过滤器
filters: {
	Monthlysale(val){
		val=val.toString();
		if(val.length>=5){
			return (val.slice(0,val.length-4) + '万笔');
		}else{
			return (val + '笔');
		}
	}
},

mounted() { 
	this.goodscanshu=canshu;
	this.$nextTick(() => { })
},

//Vue 实例销毁后调用
destroyed() { },

components: {
	goodslist
},

watch: {}

};


